/** @jsxImportSource @emotion/react */
import { css, SerializedStyles } from "@emotion/react";
import theme from "core/styles/theme";

interface T {
  onClick: (e: any) => void;
  isActive?: boolean;
  text: string;
  styleCss?: SerializedStyles;
}
function Button({ onClick, isActive = false, text, styleCss }: T) {
  return (
    <button css={[style.button(isActive), styleCss]} onClick={onClick}>
      {text}
    </button>
  );
}

const style = {
  button: (isActive: boolean) => css`
    min-width: 70px;
    padding: 5px;
    border: 1px solid ${theme.colors.grey.light};
    border-radius: ${theme.borderRadius.large};
    background-color: ${isActive
      ? theme.colors.orange
      : theme.colors.black.deepDark};
    color: ${isActive ? theme.colors.black.deepDark : theme.colors.white.text};
  `,
};

export default Button;
